import $ from 'jquery';
import 'datatables.net'
import 'datatables.net-bs4'
import { useEffect, useState } from 'react';

const Datatable = (props) => {
    const [tmp, setTmp] = useState(false);
    useEffect(()=>{
        if(props.data.length > 0 && !tmp)
            setTimeout(function(){
                window.gridTable = $('#example').DataTable({
                    dom: 'Bfrtip',
                    buttons: [
                        'copy', 'csv', 'excel', 'pdf', 'print'
                    ]
                }); 
                //document.getElementById('example_filter').remove()
                setTmp(true);
           }, 500);
    })
   
    return(
        <div>
            {
                props.data.length > 0 &&  <table id="example" className="display table table-sm table-striped">
                <thead>
                    <tr>
                        <th>İdarə</th>
                        <th>Ləqəb</th>
                        <th>Sənədin adı</th>
                        <th>Sənədin statusu</th>
                        <th>Qeydiyyat tarixi</th>
                        <th>Qısa məzmun</th>
                        <th>Təşəbbüskar</th>
                        <th>Kimin adından</th>
                        <th>Kimə göndərilir</th>
                    </tr>
                </thead>
                <tbody>
                    {
                        props.data.map(x=><tr key={x}>
                            <td>{x[0]}</td>
                            <td>{x[1]}</td>
                            <td>{x[2]}</td>
                            <td>{x[3]}</td>
                            <td>{x[4]}</td>
                            <td>{x[5]}</td>
                            <td>{x[6]}</td>
                            <td>{x[7]}</td>
                            <td>{x[8]}</td>
                        </tr>)
                    }
                </tbody>
            </table>
            }  
      </div>
    );
}

export default Datatable;